import { Box, Button, Divider, Drawer, Typography } from "@mui/material";
import MetaMaskAuth from "components/Metamask/MetamaskAuth";
import BasicModal from "components/Modal/Modal";
import { Fragment, useRef, useState } from "react";
import { defaultLand } from "types/landProps";
import styles from "./NewsLand.module.css";
import useNewsLand from "./useNewsLand";
import Clock from "./../Clock/Clock";

function NewsLand(props: any) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const {
    onMouseMove,
    clickCanvasWithCoords,
    clearLastClickedLand,
    clickedMintButton,
  } = useNewsLand(canvasRef);

  const [mintingCount, setmintingCount] = useState(0);
  const [isModalShown, setIsModalShown] = useState(false);
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const [currentLand, setCurrentLand] = useState(defaultLand);
  const todayDate = new Date().toLocaleDateString();

  const CanvasProps = {
    onMouseMove: onMouseMove,
    onClick: (e: any) => {
      clearLastClickedLand(currentLand);
      const clickedLand = clickCanvasWithCoords(e)!;
      setCurrentLand(clickedLand);
      setIsModalShown(!isModalShown);
      setIsDrawerOpen(true);
    },
  };

  return (
    <Fragment>
      <div className={styles.root}>
        {/* header */}
        <div className={styles.header} onClick={() => setIsDrawerOpen(false)}>
          <div className={styles.headerLeft}>
            <h2 className={styles.date}>{todayDate}</h2>
          </div>
          <div className={styles.headerCenter}>
            <h1 className={styles.title}>NEWSLAND TIMES</h1>
          </div>
          <div className={styles.headerRight}>
            <div className={styles.headerButtons}>
              <span className={styles.buttons}>METAMASK</span>
              <span onClick={clickedMintButton} className={styles.buttons}>
                MINTING HERE
              </span>
            </div>
            <MetaMaskAuth onAddressChanged={""} setIsLoggedIn={setIsLoggedIn} />
            {/* <Button onClick={clickedMintButton} variant="contained"> */}
            {/* Minting here */}
            {/* </Button> */}
            <Clock />
          </div>
        </div>
        {/* divider */}
        <div className={styles.divider}></div>
        <div className={styles.divider}></div>
        {/* contents */}
        <canvas
          ref={canvasRef}
          className={styles.canvas}
          {...CanvasProps}
          width={901}
          height={901}
        />
        {/* drawer */}
        <Drawer
          anchor="left"
          variant="persistent"
          open={isDrawerOpen}
          onClose={() => setIsDrawerOpen(false)}
        >
          <Box
            className={styles.drawer}
            role="presentation"
            onClick={() => setIsDrawerOpen(false)}
          >
            <Typography variant="h2">{`this is ${currentLand.id}th land `}</Typography>
            <Typography variant="h3">{`x : ${currentLand.attribute.startX}, y : ${currentLand.attribute.startY}`}</Typography>
            <Divider />
          </Box>
        </Drawer>
        {/* modal */}
        <BasicModal
          showModal={isModalShown}
          setShowModal={setIsModalShown}
          land={currentLand}
          isLoggedIn={isLoggedIn}
          canvasRef={canvasRef}
        />
      </div>
    </Fragment>
  );
}

export default NewsLand;
